body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav{
  background-color: #0277bd ;
  height: 85px;
  color: white;
  box-shadow: 0px 5px 15px grey;
  
}

.nav-item{
    margin: 25px 120px;
    font-size: 25px;
}

header {
    background: url(/clicky-game/static/media/morocco-blue.0fe4c2eb.png);
    height: 300px;
}

.header-text{
    padding-top: 150px;
    text-align: center;
    color: darkblue;
}

.footer-top{
    background-color: #0091ea ;
    height: 25px;
}
.footer-bottom{
    height: 35px;
    color: white;
    margin: 15px;
    text-align: center;
    font-weight: bold;
}

footer{
    background-color:#0277bd;
}

img{
    height: 100px;
    width: 100px
}

